import React, { Component } from "react";
import "./App.css";

export default class App extends Component {
  state = {
    total: "",
    service: "",
    partySize: "",
    totalTip: "",
    tipPP: "",
    afterTip: "",
  };

  handleTotal = (event) => {
    this.setState({
      total: event.target.value,
    });
  };

  handleService = (event) => {
    this.setState({
      service: event.target.value,
    });
  };
  handleParty = (event) => {
    this.setState({
      partySize: event.target.value,
    });
  };

  genTip = (event) => {
    event.preventDefault();
    let tip = parseFloat(this.state.total * (this.state.service / 100)).toFixed(
      2
    );
    let totalTipPP = parseFloat(tip / this.state.partySize).toFixed(2);
    let afterTipTotal = parseFloat(
      this.state.total / this.state.partySize + totalTipPP
    ).toFixed(2);
    this.setState({
      totalTip: tip,
      tipPP: totalTipPP,
      afterTip: afterTipTotal,
    });
  };

  render() {
    return (
      <div className="App">
        <h1>Tip Calculator</h1>
        <br/>
          <form onSubmit={this.genTip}>
            <div className='content'>
            <label>
              Bill Total:
              <input
                type="text"
                value={this.state.total}
                onChange={this.handleTotal}
              />
            </label>
            <label>
              Service:
              <input
                type="text"
                value={this.state.service}
                onChange={this.handleService}
              />
            </label>
            <label>
              Party Size:
              <input
                type="text"
                value={this.state.partySize}
                onChange={this.handleParty}
              />
            </label>
            </div>
            <br/>
            <input type="submit" />
          </form>
          <br/>
          <h4>Tip: ${this.state.totalTip}</h4>
          <h4>Per Person: ${this.state.tipPP}</h4>
          <h4>Each Person Pays: ${this.state.afterTip}</h4>
      </div>
    );
  }
}
